import React from "react";
import { VerticalTimeline, VerticalTimelineElement } from 'react-vertical-timeline-component';
import 'react-vertical-timeline-component/style.min.css';
import styles from "./RoadMap.module.scss";

function RoadMap() {
  return (
    <div className="relative">
      <div className="max-w-1920 mx-auto">
        <div className="flex justify-start relative px-8 transform xl:translate-y-full 3xl:translate-x-10" id="roadmap">
          <p className={styles.title}>ROADMAP</p>
        </div>
      </div>
      <div className="relative">
        <img src="/img/img_roadmap.png" alt="roadmap" className="w-full" />
        <div className="absolute container mx-auto bottom-0 lg:bottom-36 flex items-end justify-end w-full left-1/2 transform -translate-x-1/2 px-4 sm:px-8 2xl:-translate-y-20 3xl:-translate-y-32">
          <div className="bottom-0 right-0 flex flex-col items-end justify-end w-2/3 lg:w-1/2 xl:w-1/3 gap-12 3xl:w-1/2">
            <div className="uppercase text-center text-white text-xl flex justify-center flex-col items-center px-4 sm:px-8">
              <p className="whitespace-nowrap">View NFT</p>
              <p>Breakdown</p>
              <p>(pdf)</p>
              <button type="button" className="px-14 py-3 bg-app-cyan rounded-full mt-4">
                view
              </button>
            </div>
            <div className="gap-4 text-white uppercase text-xl hidden md:flex">
              <div className="text-white text-right text-xl xl:leading-10 2xl:text-xl 2xl:leading-11 3xl:text-2xl 3xl:leading-12">
                <h6>THERE ARE 5 NFTS IN TOTAL YOU CAN COLLECT
                  EACH ONE PERTAINING TO A DIFFERENT AMOUNT
                  OF REFLECTIONS. THESE 5 NFTS ALSO CREATE
                  10 DIFFERENT SETS THAT CAN BE USED
                  TO EARN STAKING REWARDS.</h6>
              </div>
              <div className="bg-gradient-to-b from-app-yellow-light to-app-yellow-dark h-90 w-3 flex-shrink-0">{" "}</div>
            </div>
          </div>
        </div>
      </div>
      <div className="flex gap-4 text-white uppercase text-xl md:hidden mt-12 px-4">
        <div className="text-white text-right text-xl xl:leading-10 2xl:text-xl 2xl:leading-11 3xl:text-2xl 3xl:leading-12">
          <h6>THERE ARE 5 NFTS IN TOTAL YOU CAN COLLECT
            EACH ONE PERTAINING TO A DIFFERENT AMOUNT
            OF REFLECTIONS. THESE 5 NFTS ALSO CREATE
            10 DIFFERENT SETS THAT CAN BE USED
            TO EARN STAKING REWARDS.</h6>
        </div>
        <div className="bg-gradient-to-b from-app-yellow-light to-app-yellow-dark h-90 w-3 flex-shrink-0">{" "}</div>
      </div>
    </div>
  );
};

export default RoadMap;